<!--  -->
<template>
  <div class="child_view">
    <album-list :keyWord="keyWord" :albumData="albumData"></album-list>
  </div>
</template>

<script>
import { HttpManager } from '../../api';

import AlbumList from '../../components/AlbumList.vue';
export default {
  name: "SearchAlbum",
  data () {
    return {
      keyWord: this.$route.query.key,
      albumData: {},
    };
  },

  components: { AlbumList, },

  methods: {
    getAlbumList (val) {
      var that = this;
      HttpManager.getSearchByKeyWord("Album", { key: this.keyWord, pn: val, rn: 30 })
        .then((res) => {
          that.albumData = Object.assign({}, that.albumData,
            {
              code: parseInt(res.code),
              total: parseInt(res.data.total),
              albumList: res.data.albumList,
              pageSize: 30,
              getData: that.getAlbumList,
            })
          // console.log(res.code);
          // that.code = parseInt(res.code)
          // that.total = parseInt(res.data.total);
          // that.albumList = res.data.albumList;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },

  computed: {},
  watch: {
    "$route.query.key": {
      handler (newVal, oldVal) {
        this.keyWord = newVal;
        this.getAlbumList(1);
      },
      immediate: true, //监听首次绑定值
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () { },

  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
  },
}

</script>
<style lang="scss" scoped>
*,
:after,
:before {
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
}
.child_view {
  min-height: 100px;
}

.name {
  font-size: 18px;
  font-weight: 400;
  line-height: 25px;
  margin-top: 10px;
  display: block !important;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  span {
    cursor: pointer;
  }
}
.time {
  color: #999;
  font-weight: 300;
  margin-top: 5px;
  margin-bottom: 26px;
  font-size: 14px;
  line-height: 1;
  font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
    sans-serif;
}
</style>
<style lang="scss">
.albums {
  .item {
    .cover_out {
      padding-bottom: 100%;
    }
  }
}
</style>



